.footer_outer_container
{
    background-color: rgb(20, 20, 20);
    color: gray;
    display: flex;
    justify-content: center;
    
}
.footer_inner_container
{
    width: 60%;
}
.footer_icons
{
    width: 12%;
    display: flex;
    justify-content: space-between;
    margin-left: 10px;
    color: #fff;

}
.footer_data
{
    display: flex;
    justify-content: space-between;
}
.footer_data ul
{
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 12vh;
}   
.footer_data ul li{
    cursor: pointer;
}
.service-code
{
    display: flex;
    justify-content: center;
    align-items: center;
    border: #fff 1px solid;
    width: 10%;
    margin: 10px 0;
    cursor: pointer;
}
.footer_copyright
{
    font-size: 12px;
    margin-bottom: 10px;
    text-align: center;
    width: 100%;
 
}