*{
    /* box-sizing: border-box; */
    color: white;
}
.row_poster{
    width: 100%;
    object-fit: contain;
    max-height: 100px;
    margin-right: 10px;
    transition: transform 450ms;
}
.row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}
.row_posters::-webkit-scrollbar{
    display: none;
}
.row_poster:hover{
    transform: scale(1.08);
}
.row_posterlarge{
    max-height: 250px;
}
.row_posterlarge:hover{
    transform: scale(1.09);
}